import Vue from 'vue'
import Vuex from 'vuex'

import TeacherService from '@/services/teacher.service'
import GroupService from "@/services/group.service";
import StudentService from "@/services/student.service";

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        teachers: [],
        groups: [],
        currentGroup: {},
        students: []
    },
    mutations: {
        setTeachers(state, teachers) {
            state.teachers = teachers
        },
        setGroups(state, groups) {
            state.groups = groups
        },
        setCurrentGroup(state, group) {
            state.currentGroup = group
        }
    },
    actions: {
        async fetchTeachers({commit}) {
            const teachers = await TeacherService.getAllTeachers()
            commit('setTeachers', teachers)
        },
        async addTeacher({dispatch}, teacher) {
            await TeacherService.addTeacher(teacher)
            dispatch('fetchTeachers')
        },
        async editTeacher({dispatch}, teacher) {
            await TeacherService.editTeacher(teacher)
            dispatch('fetchTeachers')
        },
        async deleteTeacher({dispatch}, teacher) {
            await TeacherService.deleteTeacher(teacher)
            dispatch('fetchTeachers')
        },
        async fetchGroups({commit}) {
            const groups = await GroupService.getAllGroups()
            commit('setGroups', groups)
        },
        async fetchGroupById({commit}, id) {
            const group = await GroupService.getGroup(id)
            commit('setCurrentGroup', group)
        },
        async addGroup({dispatch}, group) {
            await GroupService.addGroup(group)
            dispatch('fetchGroups')
        },
        async editGroup({dispatch}, group) {
            await GroupService.editGroup(group)
            dispatch('fetchGroups')
        },
        async deleteGroup({dispatch}, group) {
            await GroupService.deleteGroup(group)
            dispatch('fetchGroups')
        },
        async fetchStudents({commit}) {
            const students = await StudentService.getAllStudents()
            commit('setStudents', students)
        },
        async groupEditStudent({dispatch}, {student, groupId}) {
            await StudentService.editStudent(student)
            dispatch('fetchStudents')
            dispatch('fetchGroupById', groupId)
        },
        async groupUnlinkStudent({dispatch}, {studentId, groupId}) {
            await StudentService.moveStudentToGroup(studentId, null)
            dispatch('fetchGroupById', groupId)
        },
        async groupMoveStudent({dispatch}, {studentId, groupId, newGroupId}) {
            await StudentService.moveStudentToGroup(studentId, newGroupId)
            dispatch('fetchGroupById', groupId)
        },
        async addStudent({dispatch}, student) {
            await StudentService.addStudent(student)
            dispatch('fetchStudents')
        },
        async deleteStudent({dispatch}, student) {
            await StudentService.deleteStudent(student)
            dispatch('fetchStudents')
        }
    },
    modules: {}
})
