const AxiosService = require('./axios.service');

async function getAllStudents() {
    return await AxiosService.get('/student');
}

async function getStudent(id) {
    return await AxiosService.get(`/student/${id}`);
}

async function addStudent(student) {
    return await AxiosService.post('/student', student);
}

async function editStudent(student) {
    return await AxiosService.put(`/student/${student.id}`, student);
}

async function deleteStudent(student) {
    return await AxiosService.del(`/student/${student.id}`);
}

async function moveStudentToGroup(studentId, groupId) {
    return await AxiosService.put(`/student/${studentId}/group/${groupId}`);
}

module.exports = {
    getAllStudents,
    getStudent,
    addStudent,
    editStudent,
    deleteStudent,
    moveStudentToGroup
}