const AxiosService = require('./axios.service');

async function getAllTeachers() {
    return await AxiosService.get('/teacher');
}

async function addTeacher(teacher) {
    return await AxiosService.post('/teacher', teacher);
}

async function editTeacher(teacher) {
    return await AxiosService.put(`/teacher/${teacher.id}`, teacher);
}

async function deleteTeacher(teacher) {
    return await AxiosService.del(`/teacher/${teacher.id}`);
}

module.exports = {
    getAllTeachers,
    addTeacher,
    editTeacher,
    deleteTeacher
}