const AxiosService = require('./axios.service');

async function getAllGroups() {
    return await AxiosService.get('/group');
}

async function getGroup(id) {
    return await AxiosService.get(`/group/${id}`);
}

async function addGroup(group) {
    return await AxiosService.post('/group', group);
}

async function editGroup(group) {
    return await AxiosService.put(`/group/${group.id}`, group);
}

async function deleteGroup(group) {
    return await AxiosService.del(`/group/${group.id}`);
}

module.exports = {
    getAllGroups,
    getGroup,
    addGroup,
    editGroup,
    deleteGroup
}