import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '/emargement',
        alias: '/',
        name: 'Emargement',
        component: () => import('@/views/emargement/GenerateEmargement.vue')
    },
    {
        path: '/teacher',
        name: 'Teacher',
        component: () => import('@/views/teachers/TeachersListDashboard.vue'),
        children: [
            {
                path: 'new',
                name: 'AddTeacher',
                component: () => import('@/components/teachers/AddTeacherModal.vue')
            },
            {
                path: 'edit/:id',
                name: 'EditTeacher',
                component: () => import('@/components/teachers/EditTeacherModal.vue')
            },
            {
                path: 'delete/:id',
                name: 'DeleteTeacher',
                component: () => import('@/components/teachers/DeleteTeacherModal.vue')

            }
        ]
    },
    {
        path: '/group',
        name: 'Group',
        component: () => import('@/views/groups/GroupsListDashboard.vue'),
        children: [
            {
                path: 'new',
                name: 'AddGroup',
                component: () => import('@/components/groups/AddGroupModal.vue')
            },
            {
                path: 'delete/:groupId',
                name: 'DeleteGroup',
                component: () => import('@/components/groups/DeleteGroupModal.vue')
            }
        ]
    },
    {
        path: '/group/edit/:groupId',
        name: 'EditGroup',
        component: () => import('@/components/groups/EditGroupModal.vue'),
        children: [
            {
                path: 'student/new',
                name: 'GroupAddStudent',
                component: () => import('@/components/students/AddStudentModal.vue')
            },
            {
                path: 'student/edit/:studentId',
                name: 'GroupEditStudent',
                component: () => import('@/components/students/EditStudentModal.vue')
            },
            {
                path: 'student/unlink/:studentId',
                name: 'GroupUnlinkStudent',
                component: () => import('@/components/students/UnlinkStudentModal.vue')
            },
            {
                path: 'student/move/:studentId',
                name: 'GroupMoveStudent',
                component: () => import('@/components/students/MoveStudentModal.vue')
            },
            {
                path: 'student/delete/:studentId',
                name: 'GroupDeleteStudent',
                component: () => import('@/components/students/DeleteStudentModal.vue')
            }
        ]
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
