const Axios = require('axios')
// const config = require('../config')

const axiosInstance = Axios.create({
    baseURL: '/api',
    timeout: 5000,
});

/**
 * Make a GET request to the API
 * @param {string} url
 * @param {string, null} [context=null] - The object concerned by the request (e.g. Teacher, Student, etc.) * @returns {Promise<any>} - The response data
 * @author Nathan BOSCHI <nathan.boschi@edu.univ-fcomte.fr>
 */
async function get(url, context = null) {
    try {
        return (await axiosInstance.get(url)).data
    } catch (error) {
        await handleError(error, context)
    }
}


/**
 * Make a POST request to the API
 * @param url
 * @param data
 * @param {string, null} [context=null] - The object concerned by the request (e.g. Teacher, Student, etc.)
 * @returns {Promise<any>} - The response data
 * @author Nathan BOSCHI <nathan.boschi@edu.univ-fcomte.fr>
 */
async function post(url, data, context = null) {
    try {
        return (await axiosInstance.post(url, data)).data
    } catch (error) {
        await handleError(error, context)
    }
}


/**
 * Make a PUT request to the API
 * @param url
 * @param data
 * @param {string, null} [context=null] - The object concerned by the request (e.g. Teacher, Student, etc.)
 * @returns {Promise<any>} - The response data
 */
async function put(url, data, context = null) {
    try {
        return (await axiosInstance.put(url, data)).data
    } catch (error) {
        await handleError(error, context)
    }
}


/**
 * Make a DELETE request to the API
 * @param url
 * @param {string, null} [context=null] - The object concerned by the request (e.g. Teacher, Student, etc.)
 * @returns {Promise<any>} - The response data
 * @author Nathan BOSCHI <nathan.boschi@edu.univ-fcomte.fr>
 */
async function del(url, context = null) {
    try {
        return (await axiosInstance.delete(url)).data
    } catch (error) {
        await handleError(error, context)
    }
}

/**
 * Make a PATCH request to the API
 * @param url
 * @param data
 * @param {string, null} [context=null] - The object concerned by the request (e.g. Teacher, Student, etc.)
 * @returns {Promise<any>} - The response data
 * @author Nathan BOSCHI <nathan.boschi@edu.univ-fcomte.fr>
 */
async function patch(url, data, context = null) {
    try {
        return (await axiosInstance.patch(url, data)).data
    } catch (error) {
        await handleError(error, context)
    }
}

async function handleError(error, context = null) {
    let errorMessage;

    switch (error.response.status) {
        case 401:
            errorMessage = 'Please login to continue'
            break
        case 403:
            errorMessage = 'You are not authorized to access this resource'
            break
        case 404:
            if (context) errorMessage = `${context} not found`; else errorMessage = 'Resource not found'
            break
        case 500:
            errorMessage = 'We are having some trouble to process your request. Please try again later'
            break
        default:
            errorMessage = 'An error occurred'
            break
    }

    console.error(errorMessage)
    // TODO : Show error message to the user
}

module.exports = {
    get, post, put, del, patch
}